.container-wrapheaderty {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 85vh;
  background: url("../images/ty-head-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.cont-head-wrpcont {
  min-height: 380px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-family: AcuminProNormal;
}
.cont-head-wrpcont p {
  max-width: 100%;
  width: 700px;
  color: #fff;
  padding: 0 20px 0 20px;
  text-align: center;
  font-size: 17px;
}
.cont-head-wrpcont p:nth-child(3) {
  font-weight: bold;
  margin-top: -10px;
  font-size: 22px;
  text-align: center;
}

.cont-h-wrplogo {
  max-width: 100%;
  height: auto;
  max-height: 140px;
  margin-bottom: 5px;
  padding: 20px;
}

.cont-head-wrpcont-slide {
  position: relative;
  margin-top: 100px;
}
.cont-head-wrpcont-slide:before {
  content: "";
  display: block;
  position: absolute;
  width: 400px;
  height: 150px;
  top: -141px;
  left: 50%;
  -webkit-transform: translatex(-50%);
          transform: translatex(-50%);
  background: url("../images/ty-head-eco.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: top;
}

.cont-head-wrpcont-slide .container-fluid {
  padding-left: 0;
  padding-right: 0;
  background-color: #000;
}

.ty-car-pic:hover {
  cursor: pointer;
}

.carousel-item {
  height: 250px;
  width: 300px;
  background-color: #000;
}
.carousel-item i {
  font-size: 25px;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.carousel-item img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.carousel-item:hover i {
  opacity: 1;
  pointer-events: none;
}

.carousel-control-next, .carousel-control-prev {
  width: 8%;
  opacity: 0.5;
}
.carousel-control-next i, .carousel-control-prev i {
  color: #ffc13e;
  font-size: 45px;
}
.carousel-control-next:active i, .carousel-control-prev:active i {
  color: #ad8225;
}

.carousel-control-prev {
  background: -webkit-gradient(linear, left top, right top, color-stop(4%, rgb(0, 0, 0)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(90deg, rgb(0, 0, 0) 4%, rgba(0, 0, 0, 0) 100%);
}

.carousel-control-next {
  background: -webkit-gradient(linear, left top, right top, color-stop(4%, rgba(0, 0, 0, 0)), to(rgb(0, 0, 0)));
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 4%, rgb(0, 0, 0) 100%);
}

.tycar-slidepic-preview {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.7);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: none;
}

.open-tycard-prev {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.tycar-slidepic-preview-wrap {
  position: relative;
  max-width: 90%;
  width: 900px;
  height: 600px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.tycar-slidepic-preview-wrap img {
  max-width: 100%;
  height: auto;
}
.tycar-slidepic-preview-wrap i {
  position: absolute;
  right: 0;
  top: -30px;
  color: #ffc13e;
  font-size: 35px;
  cursor: pointer;
  padding: 5px;
}

@media (min-width: 768px) and (max-width: 991px) {
  /* Show 4th slide on md if col-md-4*/
  .carousel-inner .active.col-md-4.carousel-item + .carousel-item + .carousel-item + .carousel-item {
    position: absolute;
    top: 0;
    right: -33.3333%;
    /*change this with javascript in the future*/
    z-index: -1;
    display: block;
    visibility: visible;
  }
}
@media (min-width: 576px) and (max-width: 768px) {
  /* Show 3rd slide on sm if col-sm-6*/
  .carousel-inner .active.col-sm-6.carousel-item + .carousel-item + .carousel-item {
    position: absolute;
    top: 0;
    right: -50%;
    /*change this with javascript in the future*/
    z-index: -1;
    display: block;
    visibility: visible;
  }
}
@media (min-width: 576px) {
  .carousel-item {
    margin-right: 0;
  }

  /* show 2 items */
  .carousel-inner .active + .carousel-item {
    display: block;
  }

  .carousel-inner .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left),
.carousel-inner .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left) + .carousel-item {
    -webkit-transition: none;
    transition: none;
  }

  .carousel-inner .carousel-item-next {
    position: relative;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  /* left or forward direction */
  .active.carousel-item-left + .carousel-item-next.carousel-item-left,
.carousel-item-next.carousel-item-left + .carousel-item,
.carousel-item-next.carousel-item-left + .carousel-item + .carousel-item {
    position: relative;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }

  /* farthest right hidden item must be also positioned for animations */
  .carousel-inner .carousel-item-prev.carousel-item-right {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    display: block;
    visibility: visible;
  }

  /* right or prev direction */
  .active.carousel-item-right + .carousel-item-prev.carousel-item-right,
.carousel-item-prev.carousel-item-right + .carousel-item,
.carousel-item-prev.carousel-item-right + .carousel-item + .carousel-item {
    position: relative;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
    visibility: visible;
    display: block;
    visibility: visible;
  }
}
/* MD */
@media (min-width: 768px) {
  /* show 3rd of 3 item slide */
  .carousel-inner .active + .carousel-item + .carousel-item {
    display: block;
  }

  .carousel-inner .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left) + .carousel-item + .carousel-item {
